import React, {useState, useEffect} from 'react'
import PropTypes from 'prop-types'
import styled from 'styled-components';
import { graphql, Link } from 'gatsby';
import Loading from 'views/Loading/v1/Loading.js';
import Text from 'views/Text/Text/v1/Text.js';
import SiteContainer from 'views/Container/Container.js';
import UserAccount from 'views/AccountPage/user/index';
import AccountAnalytics from 'views/AccountPage/analytics/index';
import Header from 'views/Header/Header.js';
import {Helmet} from 'react-helmet';
import Meta from 'views/SEO/Meta/Meta';
import Reveal from 'views/Reveal/index.js';
import BarsBox from 'views/Box/Bars/index.js';

const AccountPage = (props) => {
 
    return <SiteContainer key="account-page" className="bg-tl-dark">
        <Helmet title={`Account Page | ${props.data.site.siteMetadata.title}`} />
        <Meta   />
        <Header center title={`Account Page`} />
        <BarsBox minHeight="60px">
              <UserAccount />
        </BarsBox>
        <AccountAnalytics />
    </SiteContainer>
}
AccountPage.propTypes = {
    data: PropTypes.shape({
      allMarkdownRemark: PropTypes.shape({
        edges: PropTypes.array,
      })
    }),
  }
export default AccountPage;

const AccountWrapper = styled.section`
border-top: 2px solid ${props => props.theme.text};
border-bottom: 2px solid ${props => props.theme.text};
max-width: ${props => props.theme.mediaL};
margin: auto;
`

export const pageQuery = graphql`
  query AccountQuery {
    allSitePage {
      edges {
        node {
          path
        }
      }
    }
    site {
      siteMetadata {
        ...siteDataFragment
      }
    }
}
`
